<template>
   <div class="main">
      <div class="head-form">
         <ul class="logo">
            <li><p>Проверь удачу</p></li>
         </ul>
         <div class="menu">
            <ul class="buttons">
               <li><img src="../assets/imgs/1.png"></li>
               <li> <button class="menu-btn" @click="showModalMenu = true"><p>Начать</p></button></li>
            </ul>
         </div>
      </div>

    <!-- Модальное окно -->
    <div v-if="showModalMenu" class="modal">
      <div class="modal-content">
        <!-- Форма для создания поста -->
        <form @submit.prevent="createPost">
          <div class="head">
            <p>Назовите себя</p>
          </div>
          <ul class="form">
            <li><input v-model="post.title" class="input_head" type="text" placeholder="Имя"></li>
          </ul>
          <div class="button_add">
            <button class="add" type="submit">Ввести</button>
            <button class="exit" @click="closeModal">Закрыть</button>
          </div>
        </form>
      </div>
    </div>

    <!-- Сообщение об ошибке -->
    <div v-if="showModalError" class="error">
      <div class="massage">
        <div class="figure">
            <img src="../assets/imgs/2.png" >
        </div>
          <div class="sms"><p>{{ massage.sms }}</p></div>
          <button class="exit-modal" @click="closeModalError">Пойти нахуй</button>
      </div>
    </div>
   </div>
</template>

<script>
export default {
  props: {
    posts: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      showModalMenu: false, // Изначально модальное окно скрыто
      showModalError: false,
      post: {
        title: '',
        body: 'осмелюсь сообщить вам, что в виду вашего неподобающего поведения, вы были официально посланы нахуй. Вы тут не первый, сюда часто попадают те, кого послали нахуй. Не беспокойтесь, просто примите это как факт и нажмите на кнопку ниже.',
      },
      massage: {
        sms: '',
      },
    };
  },
  methods: {
    openModal() {
      this.showModalMenu = true;
    },

    openModalError(){
      if (this.post.title == ''){
         this.closeModal();
         this.massage.sms = 'Введите имя!';
         this.showModalError = true;
      } else {

      this.massage.sms = this.post.title + ', ' + this.post.body;
      this.showModalError = true;
    }
   },
    closeModal() {
      this.showModalMenu = false;
      this.post.title = '';
    },

    closeModalError(){
      this.showModalError = false;
    },

    createPost() {
      this.openModalError(); 
  },

  },
};

</script>

